import React from 'react';
import developer from '../../medias/images/developer.jpg';
import Contentlogin from '../../components2/section/ContentLogin';
const Header = ()=>{
    const handlemodallogin = ()=>{
        let toggle = false;
        toggle = !toggle;
        if(toggle){
            document.querySelector('.m-l').classList.add('is-active')
        }
    }
    return(
        <section className="hero" 
            style={{background:`url(${developer})`,backgroundSize:"cover",height:"100vh"}}>
            <div className="hero-body" style={{display:"flex",alignItems:"center"}}>
                <div className="columns is-mobile re">
                    <div className="column" style={{zIndex:"5",display:"flex",justifyContent:"center",flexDirection:"column",textAlign:"center"}}>
                    <br/>
                        <p className="title anm " style={{fontFamily: "'Press Start 2P', cursive",color:"white"}}>
                            Accede a todos los cursos disponibles 
                        </p>
                        <p className="title anm " style={{fontFamily: "",color:"white"}}>
                             recuerda que para poder acceder a todos los cursos es necesario iniciar sesion
                        </p>
     
                        <div className="subtitle">
                            <div className="columns control">
                                <div className="column" style={{zIndex:"5",display:"flex",justifyContent:"center"}}>
                                    <Contentlogin/>
                                    <p  onClick={handlemodallogin} style={{zIndex:"5"}} className="button is-primary">Academia Virtual</p>
                                </div>
                            </div>
                            <div className="control">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}
export default Header;