import React from 'react';
import CardsSection from '../../components2/section/CardsSection';
import {useFade,toolspos} from '../../hooks/useMsAnimator';

const Section = ()=>{
    const num = [,1,2,3,4,5,6,7,8];
    toolspos.screenanimate=5
    toolspos.part=3
    useFade('.fade')
    return(
    <>
        <section className="section" style={{backgroundColor:"#3b055e"}}>
            <h1 className="title fade" style={{textAlign:"center",color:"white"}}>
                Contenido
            </h1>
            <hr className="fade" style={{color:"white",width:"30%",margin:"auto"}}/>
            <br/>
            <div className="columns is-multiline is-mobile fade">
                {num.map((el,i)=>(
                    <div className="column is-one-quarter-desktop" key={i} style={{minWidth:"175px"}}>
                        <CardsSection bk="#ffffff!important"/>
                    </div>
                ))}
            </div>
        </section>
        <section className="section" style={{backgroundColor:"#ffffff"}}>
            <h1 className="title fade" style={{textAlign:"center",color:"black"}}>
                Blog
            </h1>
            <hr className="fade" style={{background:"black",width:"30%",margin:"auto"}}/>
            <br/>
            <div className="columns is-multiline is-mobile fade">
            {num.map((el,i)=>(
                    <div className="column is-one-quarter-desktop" key={i} style={{minWidth:"175px"}}>
                        <CardsSection bk="#ffffff!important"/>
                    </div>
                ))}
            </div>
        </section>
    </>);
}
export default Section;